    padding: 0 40px;
  }
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  font-size: 1.6rem;
  background-image: linear-gradient(to bottom, #ffbbd2 0%, #a665ae 100%);
  box-shadow: 0 2px 1px 1px #602ebe;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: opacity 0.5s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
