.dropdown {
    position: relative;
    margin-bottom: 20px;
}

.dropdown__button {
    position: relative;
    display: block;
    width: 100%;
    text-align: left;

    background-color: #fff;
    border: 1px solid #7E9BBD;
    border-radius: 6px;
    height: 46px;

    padding-top: 13px;
    padding-bottom: 13px;
    padding-left: 20px;
    padding-right: 35px;

    font-weight: 500;
    font-size: 1.6rem;
    line-height: 150%;

    cursor: pointer;
}

.dropdown__button:focus {
    outline: none;
    box-shadow: 0 0 0 4px rgba(176, 198, 225, 0.6);
}

.dropdown__button::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translate(-50%);

    width: 0;
    height: 0;
    border-width: 10.4px 6px 0 6px;
    border-color: #79849B transparent transparent transparent;
    border-style: solid;

    pointer-events: none;
}

.dropdown__list {
    position: absolute;
    top: 52px;
    left: 0;
    margin: 0;
    padding: 0;
    list-style-type: none;

    color: $color-default-gray;
    font-size: 1.6rem;
    line-height: 150%;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(176, 198, 225, 0.6);
    overflow: hidden;

    border-radius: 6px;
    width: 100%;

    z-index: 1000;
    display: none;
}

.dropdown__list--visible {
    display: block;
}

.dropdown__list-item {
    margin: 0;
    padding: 0;

    border: 1px solid #7E9BBD;
    border-bottom: 0;
    padding: 13px 20px;

    cursor: pointer;
}

.dropdown__list-item:first-child {
    border-radius: 6px 6px 0 0;
}

.dropdown__list-item:last-child {
    border-radius: 0 0 6px 6px;
    border-bottom: 1px solid #7E9BBD;
}

.dropdown__list-item:hover {
    background-color: rgba(176, 198, 225, 0.26);
}

.dropdown__input-hidden {
    display: none;
}